/* FOUNDATION FRAMEWORK */
body{
	font-size:.875em;
}
h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
    font-weight: normal;
}
h1{font-family:Arial,Verdana,helvetica,clean,sans-serif;font-size:2.2em;line-height:1.3em;}
h2{font-family:Arial,Verdana,helvetica,clean,sans-serif;font-size:1.214em;line-height:1.25em;}
h3{font-family:Arial,Verdana,helvetica,clean,sans-serif;font-size:1.214em;line-height:1.25em}
h4{font-family:Arial,Verdana,helvetica,clean,sans-serif;font-size:1.214em;line-height:1.25em}
h5{font-family:Arial,Verdana,helvetica,clean,sans-serif;font-size:1em;line-height:1.25em;}


.rows,
.columns,
.rows:before,
.columns:before,
.rows:after,
.columns:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
#map_canvas img,
#map_canvas embed,
#map_canvas object,
.map_canvas img,
.map_canvas embed,
.map_canvas object {
  max-width: none !important;
}
.left {
  float: left !important;
}
.right {
  float: right !important;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.hide {
  display: none;
}
.antialiased {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.row {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  margin-bottom: 0;
}
.row:before,
.row:after {
  content: " ";
  display: table;
}
.row:after {
  clear: both;
}
.row.collapse > .column,
.row.collapse > .columns {
  padding-left: 0;
  padding-right: 0;
}
.row.collapse .row {
  margin-left: 0;
  margin-right: 0;
}
.row .row {
  width: auto;
  margin-left: -8px;
  margin-right: -8px;
  margin-top: 0;
  margin-bottom: 0;
  max-width: none;
}
.row .row:before,
.row .row:after {
  content: " ";
  display: table;
}
.row .row:after {
  clear: both;
}
.row .row.collapse {
  width: auto;
  margin: 0;
  max-width: none;
}
.row .row.collapse:before,
.row .row.collapse:after {
  content: " ";
  display: table;
}
.row .row.collapse:after {
  clear: both;
}
.column,
.columns {
  padding: 0;
  margin: 0;
  width: 100%;
  float: left;
}
/*[class*="column"] + [class*="column"]:last-child {
  float: right; }*/
[class*="column"] + [class*="column"].end {
  float: left;
}
